	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Vacation","date":"26 Feb 2005","slug":"/2005-02-26-vacation/","tags":"Y2005"},"html":"<p>We're gone. To Leogang. In Austria. See you. Next week.</p>"},"next":{"fields":{"title":"Night Shift","date":"23 Feb 2005","slug":"/2005-02-23-night-shift/"}},"previous":{"fields":{"title":"Back Again","date":"05 Mar 2005","slug":"/2005-03-05-back-again/"}}},{"node":{"fields":{"title":"Night Shift","date":"23 Feb 2005","slug":"/2005-02-23-night-shift/","tags":"Y2005"},"html":"<p>As you can tell from the downtime notice on <a href=\"http://blogg.de/\">blogg.de</a>, I'm putting in a night shift this evening, moving our servers into a new rack where we have lots of room for expansion. The two hour estimate is a bit pessimistic, but I've learned that what can go wrong usually does go wrong when you are overconfident.</p>\n<p>I'm getting too old for all nighters... if it comes to that, it means disaster has struck, and since PapaScott is not located at blogg.de, I'll be able to relate the sad details here. But it's not going to come to that.</p>"},"next":{"fields":{"title":"Schleswig-Holstein Counts the Votes","date":"21 Feb 2005","slug":"/2005-02-21-schleswigholstein-counts-the-votes/"}},"previous":{"fields":{"title":"Vacation","date":"26 Feb 2005","slug":"/2005-02-26-vacation/"}}},{"node":{"fields":{"title":"Schleswig-Holstein Counts the Votes","date":"21 Feb 2005","slug":"/2005-02-21-schleswigholstein-counts-the-votes/","tags":"Y2005"},"html":"<p>That's what I get for going to bed early. The official count came out at midnight, and the CDU-FDP have fallen short of a majority by 70 votes. Of the 69 seats in the state parliament, they have 34 to 33 for SPD-Green. </p>\n<p>The 2 remaining seats belong the SSW, the Danish minority party exempt from the 5% clause. They won't join a coalition, but would tolerate a minority government. The SSW supports a Scandinavian model for economic and education policies, and are more likely to support a SPD government.</p>"},"next":{"fields":{"title":"Five Questions","date":"21 Feb 2005","slug":"/2005-02-21-five-questions/"}},"previous":{"fields":{"title":"Night Shift","date":"23 Feb 2005","slug":"/2005-02-23-night-shift/"}}}],"pathPrefix":"","first":false,"last":false,"index":340,"pageCount":1101}}

